.accordion-box {
  width: 800px;
  background: #fff;
  margin: 0 auto;
  padding: 40px;
  margin-top: 40px;
  display: flex;
}
.accordion-box .heading {
  width: 40%;
  margin-right: 40px;
}
.accordion-box .heading h1 {
  color: #2b2b2b;
  position: relative;
}

.accordion-box .heading h1::before {
  content: "";
  position: absolute;
  width: 60%;
  left: 0;
  right: 0;
  bottom: -19px;
  height: 4px;
  background: orangered;
}
.qa-sec {
  width: 60%;
}
.item {
  padding: 5px 20px;
  border: 1px solid #0000001f;
  box-shadow: 0 0 15px 0 #0000001f;
  margin-bottom: 15px;
}
.item h4 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  align-items: flex-start;
  color: #2b2b2b;
}
.item p {
  color: #676767;
}
.item h4 span {
  color: orangered;
  font-size: 22px;
  cursor: pointer;
}
